import React, { Component } from 'react';

class Legend extends Component {
    render() {
        return (
            <div id="legend-box">
                <p><span className="lv0 legend"></span>等級〇：沒去過</p>
                <p><span className="lv1 legend"></span>等級一：經過</p>
                <p><span className="lv2 legend"></span>等級二：旅遊</p>
                <p><span className="lv3 legend"></span>等級三：短居</p>
                <p><span className="lv4 legend"></span>等級四：居住</p>
                <p><span className="lv5 legend"></span>等級五：久居</p>
            </div>
        );
    }
}
export default Legend;


